.search-bar {
    --size: 35px;
    position: relative;
    display: flex;
    width: 30%;
    border-radius: 50px;
    border: gainsboro 1px solid;
    padding: 2px;
}

.search-bar__input {
    flex-grow: 1;
    height: var(--size);
    padding: 0 1em;
    font-size: 1.4rem;
    background-color: transparent;
    border: 0;
}

.search-bar__input:focus {
    outline: 0;
}

.search-bar__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    cursor: pointer;
    border: 0;
    border-radius: 50px;
    width: var(--size);
    height: var(--size);
    background-color: $primary;
    color: #fff;
}
