.table-container {
    height: 32rem;
    overflow: scroll;
    box-shadow: 0 0 20px rgba(0, 0, 0, .15);
    width: fit-content;
}

.table {
    border-collapse: collapse;
    font-size: 0.9em;
    min-width: 40rem;
}

// Sticky header
.table.sticky th {
    position: sticky;
    position:-webkit-sticky;
    top: 0;
    background-color: rgb(58, 218, 218);
}

.table.sticky th::after {
    content: '';
    width: 100%;
    height: 2px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: mix(black, $primary, 10%);
}

.table thead tr {
    background-color: $primary;
    color: #fff;
    text-align: left;
    font-weight: bold;
}

.table th, 
.table td {
    padding: 12px 30px;
}

.table tbody tr {
    border-bottom: 1px solid #dddddd;
}

.table tbody tr.active {
    color: $primary;
    font-weight: bold;
}
 
.table tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.table tbody tr:last-of-type {
    border-bottom: 2px solid $primary;
}

tr[data-href] {
    cursor: pointer;
}


@media only screen and (max-width : $breakpoint-lg) { 
    .table-container {
        max-width: 100%;
    }
}